import { Tooltip, TooltipTrigger, TooltipContent } from "./ui/tooltip"
import React, {
  ChangeEvent,
  HTMLInputTypeAttribute,
  ReactElement,
  SetStateAction,
  useState,
} from "react"
import { Input } from "./ui/input"
import { Slider } from "./ui/slider"
import { useBreakpoint } from "../hooks/useBreakpoint"

interface ParameterSliderProps {
  title: string
  type: HTMLInputTypeAttribute | undefined
  min: number
  max: number
  step: number
  defaultValue: number
  normalizeInputData: (value: string) => any
  normalizeSliderData?: Function
  setParentState: SetStateAction<any>
  tooltipContent: ReactElement
}

const ParamaterSlider: React.FC<ParameterSliderProps> = ({
  title,
  defaultValue,
  min,
  max,
  step,
  normalizeInputData,
  normalizeSliderData,
  type = "number",
  tooltipContent,
  setParentState,
}) => {
  const [value, setValue] = useState(defaultValue)
  const { isLg } = useBreakpoint("lg")

  // prevents slider from overflowing if value is > max val (can happen if user types in big number in input before unfocusing)
  const sliderValue = Math.max(
    Number(min),
    Math.min(Number(max), Number(value))
  )

  return (
    <div className="">
      <Tooltip delayDuration={300} skipDelayDuration={150}>
        <TooltipTrigger asChild>
          <div>
            <span className="flow-root inline-block align-middle mb-3">
              <p className="text-sm font-medium float-left align-text-top">
                {title}
              </p>
              <Input
                className="float-right h-6 p-0 w-12"
                type={type}
                value={[value.toString()]}
                step={step}
                onChange={(e) => {
                  if (normalizeInputData) {
                    let normalized = normalizeInputData(e.target.value)
                    const val = e.target.value
                    console.log({ val, normalized })

                    setValue(normalized)
                    setParentState(normalized)
                  }
                }}
                onBlur={(e) => {
                  let normalized = Math.max(
                    Number(min),
                    Math.min(Number(max), Number(value))
                  )
                  setValue(normalized)
                }}
                min={min}
                max={max}
              />
            </span>
            <Slider
              defaultValue={[value]}
              value={[sliderValue]}
              min={min}
              max={max}
              step={step}
              onValueChange={(e) => {
                setValue(e[0])
              }}
              onValueCommit={(e) => {
                setValue(e[0])
                setParentState(e[0])
              }}
            />
          </div>
        </TooltipTrigger>
        <TooltipContent side={isLg ? "left" : "bottom"}>
          {tooltipContent}
        </TooltipContent>
      </Tooltip>
    </div>
  )
}

export default ParamaterSlider
